/**
* Test whether all elements in a collection are truthy.
*
* @module @stdlib/utils-every
*
* @example
* var every = require( '@stdlib/utils-every' );
*
* var arr = [ 1, 1, 1, 1, 1 ];
*
* var bool = every( arr );
* // returns true
*/

// MODULES //

var every = require( './main.js' );


// EXPORTS //

module.exports = every;
